.main-sidebar {
  margin-block-start: 4rem; //3.72rem
  padding-block-start: 0.5rem;
  padding-block-end: 5rem;
  height: 100vh;
  position: relative;
  overflow: auto;
}
.app-sidebar {
  width: 15rem;
  height: 100%;
  background: $menu-bg;
  border-inline-end: 1px solid $menu-border-color;
  position: fixed;
  inset-block-start: 0;
  inset-inline-start: 0;
  z-index: 103;
  transition: all 0.1s ease-out;
  .main-sidebar {
    box-shadow: 0.125rem 0 0 rgba(10, 10, 10, 0.04);
  }
  .main-sidebar-header {
    height: 4rem;
    width: 14.95rem;
    position: fixed;
    display: flex;
    background: $menu-bg;
    z-index: 9;
    align-items: center;
    justify-content: center;
    padding: 1rem 1.25rem;
    // border-inline-end: 1px solid $menu-border-color;
    border-block-end: 1px solid $menu-border-color;
    transition: all 0.1s ease-out;
    .header-logo {
      img {
        margin: 0 auto;
      }
      .desktop-logo {
        display: flex;
      }
      .desktop-dark,
      .toggle-logo,
      .toggle-dark {
        display: none;
      }
    }
  }
  .slide__category {
    color: $menu-prime-color;
    font-size: 0.6rem;
    font-weight: 600;
    letter-spacing: 0.065rem;
    text-transform: uppercase;
    padding: 0.75rem 1.65rem;
    white-space: nowrap;
    opacity: 0.5;
    position: relative;
  }
  .slide {
    margin: 0;
    padding: 0rem 0.75rem;
  }
  .side-menu__item {
    padding: 0.75rem;
    position: relative;
    display: flex;
    align-items: center;
    text-decoration: none;
    font-size: 0.78rem;
    color: $menu-prime-color;
    font-weight: 400;
    border-radius: $default-radius;
    &.active,
    &:hover {
      color: $menu-prime-color;
      background-color: rgba(var(--light-rgb), 0.5);
      .side-menu__label,
      .side-menu__angle {
        color: $menu-prime-color;
      }
      .side-menu__icon {
        fill: $menu-prime-icon-color;
        color: $menu-prime-icon-color;
      }
    }
    &.active {
      font-weight: normal;
    }
  }
  .slide-menu {
    &.child1,
    &.child2,
    &.child3 {
      .side-menu__item.active {
        background-color: transparent;
        &:hover {
          background-color: $white-1;
        }
      }
    }
  }
  .slide-menu {
    padding: 0rem;
  }
  .slide-menu {
    &.child1,
    &.child2 {
      .side-menu__item {
        padding: 0.45rem 1.6rem;
      }
    }
  }
  .slide-menu {
    &.child1,
    &.child2,
    &.child3 {
      .side-menu__item {
        &:before {
          position: absolute;
          content: "";
          width: 0.4rem;
          border: 1px solid $menu-prime-color;
          border-radius: 50%;
          background-color: transparent;
          inset-inline-start: 0.5rem;
          opacity: 0.5;
        }
        &:hover {
          color: $menu-prime-color;
          &:before {
            content: "";
            border: 1px solid $menu-prime-color;
          }
        }
        &.active {
          &:before {
            position: absolute;
            content: "";
            width: 0.4rem;
            border: 1px solid $menu-prime-color;
            border-radius: 50%;
            background-color: transparent;
            opacity: 1;
          }
        }
      }
      li {
        padding: 0;
        padding-inline-start: 1rem;
        position: relative;
      }
    }
  }
  .side-menu__label {
    white-space: nowrap;
    color: $menu-prime-color;
    position: relative;
    font-size: 0.85rem;
    line-height: 1;
    vertical-align: middle;
  }
  .side-menu__icon {
    margin-inline-end: 0.625rem;
    width: 1.15rem;
    height: 1.15rem;
    font-size: 1.15rem;
    text-align: center;
    color: $menu-prime-icon-color;
    fill: $menu-prime-icon-color;
    border-radius: $default-radius;
    line-height: normal;
  }
  .side-menu__angle {
    transform-origin: center;
    position: absolute;
    inset-inline-end: 0.75rem;
    font-size: 0.85rem !important;
    color: $menu-prime-color;
    transition: all 0.05s ease;
  }
  .slide.side-menu__label1 {
    display: none;
  }
}
.horizontal-logo {
  padding: 0.85rem 0rem;
}
.slide.has-sub .slide-menu {
  transform: translate(0, 0) !important;
  visibility: visible !important;
}
.nav ul li {
  list-style-type: none;
}
.nav > ul {
  padding-inline-start: 0px;
}
.slide-menu {
  display: none;
}
.slide.has-sub {
  display: grid;
  &.open {
    > .side-menu__item .side-menu__angle {
      transform: rotate(90deg);
    }
  }
}
@media (min-width: 992px) {
  [data-toggled="open"] {
    .page {
      position: absolute;
    }
  }
  [data-toggled="menu-click-closed"],
  [data-toggled="menu-hover-closed"],
  [data-toggled="icon-click-closed"],
  [data-toggled="icon-hover-closed"],
  [data-toggled="icon-text-close"] {
    .app-content {
      min-height: calc(100vh - -23rem);
    }
  }
}

/* Responsive Styles Start */

@media (max-width: 991.98px) {
  .horizontal-logo {
    .header-logo {
      .desktop-logo,
      .desktop-dark,
      .toggle-dark {
        display: none;
      }
      .toggle-logo {
        display: block;
      }
    }
  }
  .main-content {
    padding-block-start: 2.5rem;
  }
  .main-sidebar-header {
    display: none !important;
  }
  .main-sidebar {
    margin-block-start: 0;
  }
  .app-sidebar {
    inset-block-start: 0;
  }
  .main-menu {
    margin: 0 !important;
  }
  .rounded-header{
    display: none;
  }
}
// .simplebar-content {
//   padding: 12px 0 !important; 
// }
.slide-left,
.slide-right {
  display: none;
}
[data-nav-layout="vertical"] {
  .slide.has-sub {
    &.active {
      > .side-menu__item {
        margin-block: 0.25rem;
      }
    }
  }
}
@media (min-width: 992px) {
  .horizontal-logo {
    display: none;
  }
}
/* Responsive Styles End */

/* Responsive Overlay Start */
#responsive-overlay {
  visibility: hidden;
  position: fixed;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  z-index: 101;
  background-color: rgba(15, 23, 42, 0.5);
  transition-property: all;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 100ms;
  &.active {
    visibility: visible;
  }
}
[data-toggled] {
  .app-sidebar .main-sidebar-header {
    border-inline-end: 1px solid $menu-border-color;
  }
}
/* Responsive Overlay End */
